/* Game.css */

.game {
    text-align: center;
    padding: 2rem;
}

.game h1 {
    font-size: 2.5rem;
    margin-bottom: 2rem;
}

.guess-buttons {
    margin: 1rem 0;
}

.guess-buttons button {
    background-color: #1976d2;
    color: #fff;
    border: none;
    padding: 0.75rem 1.5rem;
    margin: 0 0.5rem;
    font-size: 1rem;
    cursor: pointer;
    border-radius: 5px;
    transition: background-color 0.3s ease;
}

.guess-buttons button:hover {
    background-color: #1565c0;
}

.guess-buttons button:disabled {
    background-color: #ccc;
    cursor: not-allowed;
}

.loading {
    font-size: 1.2rem;
    margin: 1rem 0;
}

.results {
    margin-top: 2rem;
}

.results h2 {
    font-size: 2rem;
    margin-bottom: 1rem;
}

.results p {
    font-size: 1.1rem;  /* Ensure consistent font size */
}

.scoreboard {
    margin-top: 2rem;
}

.scoreboard h3 {
    font-size: 1.5rem;
    margin-bottom: 1rem;
}

.scoreboard p {
    font-size: 1.1rem;
}

.emoji-scoreboard {
    margin-top: 2rem;
    text-align: center;
}

.emoji-scoreboard h3 {
    font-size: 1.5rem;
    margin-bottom: 1rem;
}

.emoji-container {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.emoji {
    font-size: 2rem;
    margin: 0.5rem;
}
