/* FileUpload.css */

.file-upload {
    margin: 2rem 0;
    text-align: center; /* Center the content */
}

.file-upload img {
    max-width: 600px; /* Increase max width */
    width: 100%;      /* Ensure the image scales responsively */
    height: auto;
    border: 2px solid #ccc;
    border-radius: 10px;
}

.file-upload p {
    font-size: 1rem;
    margin-top: 0.5rem;
    color: #555;
}
